import React from "react";
import { useEffect, useState } from "react";
import Starter from "../views/Starter";
import Headers from "./Headers";
import Footers from "./Footers";
import Loader from "./loader/Loader";
// git remote add origin https://github.com/gitahope/multiplication-tables.git
// https://github.com/thepmsquare/multiplication-tables-exercise-react
// https://www.mathmammoth.com/practice/

const FullLayout = () => {
  const [num, setNum] = useState(1);
  const [minHeight, setMinHeight] = useState(100);
  const [paddingTop, setPaddingTop] = useState(40);
  const [paddingBottom, setPaddingBottom] = useState(40);
  const [isLoading, setIsLoading] = useState(true);

  const increment=()=>{ if(num<30){ setNum(num+1); } }
  
  const decrement=()=>{ if(num>1){ setNum(num-1); } }

  const handleSelected=(e)=>{
    console.log(e.target.value);
    setNum(parseInt(e.target.value, 10));
  }

  const resizeWindow = () => {
    let elh = document.querySelector('.header.position-fixed');
    let elf = document.querySelector('.footer');
    setMinHeight(window.innerHeight);
    setPaddingTop(elh.offsetHeight  + 10);
    setPaddingBottom(elf.offsetHeight  + 10);
    setTimeout(()=>{ setIsLoading(false) },1300);
  };

  useEffect(() => {
    resizeWindow();
    window.addEventListener("load", resizeWindow);
    window.addEventListener("resize", resizeWindow);
    return () => {
      window.removeEventListener("load", resizeWindow)
      window.removeEventListener("resize", resizeWindow)
    };
  }, []);

  return (
    <React.Fragment>
      <main className="h-100" style={{minHeight:minHeight,paddingTop:paddingTop,paddingBottom:paddingBottom}}>
          {/********header**********/}
          <Headers selected={handleSelected} />
        {isLoading && <Loader />}
        {!isLoading && <Starter number={num} minus={decrement} plus={increment} />}
      </main>
      
      <Footers plus={increment} minus={decrement} number={num} />
      
    </React.Fragment>    
  );
};

export default FullLayout;
